import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip, Select, Collapse } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/employee/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Employee extends Component {
  state = {
    filter: {
      id_job_title: null,
      id_department: null,
      id_location: null,
      sort_by: "employee_code",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getEmployees } = this.props;
    getEmployees();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.employee.error) {
      notification("error", this.props.employee.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.employee.success) {
      notification("success", this.props.employee.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getEmployees } = this.props;
    this.setState({
      filter: {
        id_job_title: null,
        id_department: null,
        id_location: null,
        sort_by: "employee_code",
        sort_type: "asc",
        pagination: null
      }
    });
    getEmployees(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getEmployees } = this.props;
    this.setState({
      filter: {
        id_job_title: null,
        id_department: null,
        id_location: null,
        sort_by: "employee_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getEmployees();
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getEmployees } = this.props;
    //const { current_page } = this.props.employee;
    const { filter } = this.state;
    let string = "";

    Object.keys(filter).map(key => filter[key]
      ? string
        ? (string += `&${key}=${filter[key]}`)
        : (string += `${key}=${filter[key]}`)
      : null);
    // console.log(string);
    this.setState({ search: null });
    getEmployees(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getEmployees } = this.props;
    this.setState({
      filter: {
        id_job_title: null,
        id_department: null,
        id_location: null,
        sort_by: "employee_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getEmployees();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateEmployee(id);
    } else {
      this.props.deactivateEmployee(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getEmployees } = this.props;
    const { search } = this.props.employee;
    // const { filter } = this.props.employee;
    getEmployees(page, search);
  };

  listAction = {
    title: "",
    dataIndex: "id_employee",
    rowKey: "id_employee",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="employees.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
            <Popconfirms
              title={<IntlMessages id="employees.popconfirm.activate" />}
              onConfirm={this.handleRecord.bind(this, "activate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.activate" />}
              >
                <Button size="small" className="invoiceRstBtn">
                  <i className="ion-refresh" />
                </Button>
              </Tooltip>
            </Popconfirms>
          )}
      </div>
    )
  };

  render() {
    // console.log(this.props.employee);
    // console.log(this.state.filter);
    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "employee_code",
        rowKey: "employee_code",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "employees.add.firstName" }),
        dataIndex: "first_name",
        rowKey: "first_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "employees.add.lastName" }),
        dataIndex: "last_name",
        rowKey: "last_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "jobTitles.pageHeader" }),
        dataIndex: "job_title.job_title_name",
        rowKey: "job_title.job_title_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.department" }),
        dataIndex: "department.department_name",
        rowKey: "department.department_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "registeredAt" }),
        dataIndex: "registered_at.location_name",
        rowKey: "registered_at.location_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { employees } = this.props.employee;
    //console.log(this.props.employee); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Employee") && columns.length === 7) {
      columns.push(this.listAction);
    }

    Object.keys(employees).map((employee, index) => {
      return dataSource.push({
        ...employees[employee],
        key: employee
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.employee;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="employees.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Employee") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="employees.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "code" }) + ", " + getLanguage({ id: "employees.add.firstName" }) + ", " + getLanguage({ id: "employees.add.lastName" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="jobTitles.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_job_title"
                      )}
                      value={
                        this.state.filter.id_job_title
                          ? this.state.filter.id_job_title
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="employees.filter.select.jobTitle" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.employee.job_titles.map((data, key) => (
                        <Option key={key} value={data.id_job_title}>
                          {data.job_title_name}
                        </Option>
                      ))}
                    </Select>
                  </div>
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="employees.filter.departments" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_department"
                      )}
                      value={
                        this.state.filter.id_department
                          ? this.state.filter.id_department
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="employees.filter.select.department" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.employee.departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {data.department_name}
                        </Option>
                      ))}
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="employees.filter.registeredAt" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="employees.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.employee.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_name}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "16.6%" }}>
                    <label>
                      <IntlMessages id="sortBy" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "sort_by"
                      )}
                      value={
                        this.state.filter.sort_by
                          ? this.state.filter.sort_by
                          : null
                      }
                      style={{ width: "90%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value="employee_code">
                        <IntlMessages id="employees.add.code" />
                      </Option>
                      <Option key={2} value="first_name">
                        <IntlMessages id="employees.add.firstName" />
                      </Option>
                      <Option key={3} value="last_name">
                        <IntlMessages id="employees.add.lastName" />
                      </Option>
                    </Select>
                  </div>

                  <div style={{ width: "16.6%" }}>
                    <label>
                      <IntlMessages id="sortType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "sort_type"
                      )}
                      value={
                        this.state.filter.sort_type
                          ? this.state.filter.sort_type
                          : null
                      }
                      style={{ width: "90%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value="asc">
                        <IntlMessages id="ascending" />
                      </Option>
                      <Option key={2} value="desc">
                        <IntlMessages id="descending" />
                      </Option>
                    </Select>
                  </div>

                  <div style={{ width: "16.6%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "85%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.employee.loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "24px" }}
                >
                  <Button
                    size="small"
                    type="primary"
                    style={{ marginRight: "10px" }}
                    onClick={this.handleFilter}
                  >
                    <IntlMessages id="filter.submit" />
                  </Button>
                  <Button onClick={this.handleResetFilter} size="small">
                    <IntlMessages id="filter.reset" />
                  </Button>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {employees.length === 0 && !this.props.employee.loading ? (
              <Empty
                description={<IntlMessages id="employees.table.noData" />}
              />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={dataSource}
                      columns={columns}
                      loading={this.props.employee.loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    employee: state.employee,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(Employee);
