const actions = {
  getEmployees: (page, search, filter) => ({
    type: "GET_EMPLOYEES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
    }
  }),
  createEmployee: () => ({
    type: "CREATE_EMPLOYEE_REQUEST",
  }),
  storeEmployee: data => ({
    type: "STORE_EMPLOYEE_REQUEST",
    payload: { body: data }
  }),
  editEmployee: id_employee => ({
    type: "EDIT_EMPLOYEE_REQUEST",
    payload: { id_employee: id_employee }
  }),
  updateEmployee: (id_employee, data) => ({
    type: "UPDATE_EMPLOYEE_REQUEST",
    payload: {
      id_employee: id_employee,
      body: data
    }
  }),
  showEmployee: id_employee => ({
    type: "SHOW_EMPLOYEE_REQUEST",
    payload: { id_employee: id_employee }
  }),
  deactivateEmployee: id_employee => ({
    type: "DEACTIVATE_EMPLOYEE_REQUEST",
    payload: { id_employee }
  }),
  activateEmployee: id_employee => ({
    type: "ACTIVATE_EMPLOYEE_REQUEST",
    payload: { id_employee }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
